.holder {
  border: 3px solid rgba(0, 0, 0, 0);
  margin: 15px;
  width: 50px;
  height: 50px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 18px;
  cursor: pointer;
}

.active {
  animation: activeHolder 4s infinite;
}

.active.big {
  transform: scale(1.4);
}

@keyframes activeHolder {
  0% {
    border-color: #d57424;
  }

  25% {
    border-color: #feda77;
  }

  50% {
    border-color: #dd2a7b;
  }

  75% {
    border-color: #8134af;
  }

  100% {
    border-color: #515bd4;
  }
}

@media screen and (max-width: 500px) {
  .holder {
    margin: 10px;
  }
}

@media screen and (max-width: 420px) {
  .holder {
    width: 40px;
    height: 40px;
    font-weight: 700;
    font-size: 14px;
  }
}

@media screen and (max-width: 360px) {
  .holder {
    width: 37px;
    height: 37px;
  }
}

@media screen and (max-width: 350px) {
  .holder {
    width: 33px;
    height: 33px;
  }
}
